import React from 'react';

const Section = (props) => (
  <div className='section'>
    <div className='container'>
      <span className='text-muted text-small'>{props.post.frontmatter.timeframe}</span>
      <div className='d-flex justify-content-between'>
        <h2 className='mt-0'>{props.post.frontmatter.title}</h2>
        {props.is_post ? null : <a href={props.post.frontmatter.path}>View</a>}
      </div>
        <div className='mb-5'
        dangerouslySetInnerHTML={{
            __html: props.post.excerpt,
        }} />
    </div>
    <div className='container section-header mb-5'>
      <div 
        className='gradient d-flex justify-content-center flex-wrap'
        style={{ background: `linear-gradient(135deg, ${props.post.frontmatter.gradient.left} 15%, ${props.post.frontmatter.gradient.right} 85% )` }}
      >
        <img src={require(`../pages${props.post.frontmatter.path}/images/${props.post.frontmatter.images.first}.png`)}/>
        { props.post.frontmatter.images.second ? <img src={require(`../pages${props.post.frontmatter.path}/images/${props.post.frontmatter.images.second}.png`)}/> : null}
      </div>
    </div>
  </div>
);

export default Section;



